.result {
    &__pic {
        max-width: 525px;
        height   : 355px;
    }

    &__img {
        object-fit: scale-down;
    }

    @media(max-width: $tablet) {
        &__block {
            flex-direction: column;
        }

        &__pic {
            height: auto;
        }
    }
}