.basket {
    &__pic {
        min-width: 150px;
    }

    &__del {
        width : var(--size-10);
        height: var(--size-10);
    }

    &__promo {
        background: radial-gradient(81.43% 266.13% at 82.08% 89.23%, #FEFC4A 0%, #F3A712 60.81%);
    }

    &__sidebar {
        max-width: 360px;
    }

    &__block {
        position: sticky;
        top     : 80px;
    }

    &__die {
        background-color: var(--color-red-20);
    }

    &__link {
        border-bottom: 1px dashed var(--color-black);
    }

    @media(max-width: $laptop) {
        &__content {
            flex-direction: column;
        }

        &__sidebar {
            max-width: 100%;
        }

        &__block {
            position: static;
        }
    }
}